.movie {
  background-color: white;
  margin-bottom: 70px;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  display: flex;
  /* grid-template-columns: minmax(150px, 1fr) 2fr; */
  /* grid-gap: 20px; */
  text-decoration: none;
  color: inherit;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);

  flex-direction: column;
}

.movie__img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__title,
.movie__year {
  margin: 0;
  font-weight: 300;
  text-decoration: none;
}

.movie__title a {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
  text-decoration: none;
}

.movie__genres {
  /* list-style: none; */
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}

.movie__genres li,
.movie__year {
  margin-right: 10px;
  font-size: 14px;
}

.movie__elements {
  display: flex;
  margin-bottom: 30px;
  flex-direction: row;
  flex: 1;
}

.movie__elementHeader {
  display: flex;
  flex: 3;
  margin-bottom: 30px;
}

.movie__elementItem {
  display: flex;
  flex: 7;
  margin-bottom: 30px;
}

.movie__elementItem img {
  width: 100%;
}

.movie__elementsTest {
  display: flex;
  margin-bottom: 30px;
  flex-direction: row;
  align-content: space-between;
  flex: 1;
}

.movie__elementTest {
  width: 30px;
  height: 30px;
  margin-bottom: 10px;
  background-color: blue;
}

.header2 {
  font-size: 32px;
  font-weight: bold;
  text-align: center;
}

.header3 {
  font-size: 20px;
  font-weight: 500;
  display: block;
}